export default {
  name: 'couponused',
  props:['value'], 
  data () {
    return {
      selData:{
        id: this.value.id,
        momey: this.value.momey,
        coupon: this.value.coupon,
        couponMsg: this.value.couponMsg,
        couponStart: this.value.couponStart,
        couponEnd: this.value.couponEnd,
      },
    }
  },
  methods: {
    
  }
}



